.cart-container-item {
    display: flex;
    margin: 1em;
    padding: 1em;
    max-height: 15em;
    background-color: rgb(250, 250, 250);
}

.cart-container-item-image {
    max-width: 10em;
    max-height: 10em;
}

.cart-container-item-detail {
    display: flex;
    flex-direction: column;
    margin: 1em;
}

.cart-container-item-title {

    max-width: 30em;
    overflow: hidden;
    color: black;
    font-weight: bold;
}

.cart-container-button-remove-item{
    padding: .5em;
    border: none;
    background-color: crimson;
    color: white;
    font-weight: bold;
    max-width: 10em;
    justify-self: left;
}

.cart-container-button-remove-item:hover{
    background-color: rgb(70, 2, 19);
}

.cart-container-button-remove-item:disabled{
    background-color: rgb(148, 148, 148);
}