.wrapper {
  height: 100%;
  background-color: rgb(195, 195, 195);
  
}






.main-container {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  overflow: hidden;
}



.main-area {
  flex: 5;
  background-color: white;
  
}

.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  max-width: 70%;
  margin: 0em auto;
  background-color: white;
}

.sidebar {
  background-color: rgb(235, 232, 232);
  flex: 1;
}


.sidebar__list-item {

  display: flex;
  flex-direction: column;
  border: none;
}

.sidebar-button {
  background: transparent;
  width: 15em;
  border: none;
  padding: .5em;
  font-weight: 500;
  outline: none;
}

.sidebar-button:hover {
  background: yellow;
}

.sidebar-button>a {
  text-decoration: none;
  color: black;
}

.sidebar-button>a:hover {
  text-decoration: none;
  color: black;
}


.sidebar__list-item-header {
  padding-top: 1em;
  padding-bottom: .5rem;
  align-self: center;
}

.sidebar-list {
  display: flex;
  flex-direction: column;
  margin: 1em 0em;
  padding: 0em;
}

.sidebar-list li {
  list-style-type: none;
  padding: .5em 1em;
  font-weight: bold;
}

.sidebar-list li:hover {
  background-color: #fc7e7e;
  cursor: pointer;
}




@media only screen and (max-width: 600px) {
  .main-container {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    flex-wrap: wrap;
  }

  .main-area {
    order: 1;
  }

  .sidebar {
    display: none;
  }
  .products-container {
    max-width: 90%;
   }
}

.not-found{
  margin: 2em 2em;
  color: crimson;

}













.pagination-container{
  display: flex;
  justify-content: center;
  background-color: rgb(240, 240, 240);;
}


.pagination {
  margin: 2em auto;
  display: flex;
  list-style: none;
  outline: none;
  justify-content: center;
  color: black;
  font-weight: bolder;
}
.pagination > .active > a{
  background-color: #FF1B1C ;
  border-color: #FF1B1C ;
  
}
.pagination > li > a{
  border: 1px solid #FF1B1C ;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a, .pagination > .active > span, .pagination > .active > a:hover, .pagination > .active > span:hover, .pagination > .active > a:focus, .pagination > .active > span:focus{
  background-color: #FF1B1C ;
  border-color: #FF1B1C;
  outline: none ;
}
.pagination > li > a, .pagination > li > span{
  color: black
}
.pagination > li:first-child > a, .pagination > li:first-child > span, .pagination > li:last-child > a, .pagination > li:last-child > span{
  border-radius: unset
}