.nav-container {

    display: flex;
    flex-direction: column;

}

.header-nav {
  
    color: #FF1B1C;
    background-color: white;
    box-shadow: 0em 0em .15em .01em #888888;    
    z-index: 1;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.header-nav__search {

    align-items: center;
    margin: 0em 1em;
    flex: 1;
    
}

.header-nav__div {
    padding: .25em;
    display: flex;
    align-items: center;
    object-fit: contain;
    flex: 1;
}
.cart-num{
    color: rgb(105, 0, 0);
}

.header-nav__div>input[type=text] {
    border-radius: 1rem 0rem 0rem 1rem;
    padding: 0.25rem 0.25rem 0.25rem 1rem;
    border-right: none;
    border: 1px solid #FF1B1C;
    border-right: none;
    justify-self: center;
    flex: 1;
}

.search-btn {
    padding: 0.25rem .5rem;
    object-fit: scale-down;
    border-radius: 0rem 1rem 1rem 0rem;
    border: 1px solid #FF1B1C;
    background-color: #FF1B1C;
    color: white;
}
.clear-btn {
    padding: 0.25rem .5rem;
    object-fit: scale-down;
    border: 1px solid #FF1B1C;
    background-color: white;
    border-left: none;
}




.header-nav__links {
    display: flex;
    padding: 0.5em 1em;
}



.header-nav-brand {
    font-weight: 800;
    padding: 0.5em 1em;
}

.header-nav-active {
    border-bottom: 3px solid #FF1B1C;
}


.header-nav__links a,p {
    display: flex;
    padding: .2em;
    margin: .2em;
    font-size: 1em;
    color: #FF1B1C;
    font-weight: bold;
    align-items: center;

}


.horizontal-scrollable-contaner {
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    background-color: #f1f1f1;
    color: #FF1B1C;
    font-weight: bold;
    display: flex;
    align-items: center;
}



.horizontal-nav {
    display: flex;
    margin: 0em;
    padding: 0em;
}

.horizontal-nav-inactive-li {
    text-decoration: none;
    padding: 1em;
    border: none;
    background-color: transparent;
    color: #FF1B1C;
    list-style-type: none;
}

.horizontal-nav-active {
    text-decoration: none;
    padding: 1em;
    border: none;
    background-color: transparent;
    color: white;
    font-weight: bold;
    background-color: #FF1B1C;
    list-style-type: none;
}

.horizontal-nav li:hover {
    background-color: yellow;
    color: black;
    font-weight: bold;
    cursor: pointer;
}


@media only screen and (max-width: 600px) {
    .header-nav {
        flex-direction: column;
    }


    .header-nav__search {
        margin-top: 0rem;
    }

    .header-nav__links {
        padding-bottom: 1rem;
    }


}