.cart-container{
    display: flex;
    flex-direction: column;
    
}

.cart-container-list{
    display: flex;
    flex-direction: column;    
    margin: 0em auto;
    padding: 2em;    
    box-shadow: .1px .1px 2px  #888888;
}
.list-item-text{
    margin: 1em auto;
}

.cart-message{
    margin: 2em auto;
    color: crimson;
    text-align: center;
}

.process-check-out{
    padding: 1em;
    margin: 1em auto;
    background-color: yellow;
    font-weight: bold;
    border: none;
    box-shadow: .1px .1px 2px  #888888;
}