.product {
    display: flex;
    flex-direction: column;
    margin: .25rem;
    padding: 1rem;
    max-width: 15rem;

    background-color: white;
    margin-top: 1rem;
    box-shadow: .1px .1px 2px  #888888;
}

.product > img {

    max-width: 10rem;
    width: 10em;
    
    width: 10rem;
    align-self: center;
    padding: 1em;
    
}

.checked {
    color: orange;
  }


@media only screen and (max-width: 600px) {
    .product{
        max-width: 20rem;
    }

}