.signin{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.form-container{
    box-shadow: 1px 1px 4px 1px #d0d0d0;
    display: flex;
    flex-direction: column;
   padding: 2em;

}

.signin-container-header{
    background-color: #FF1B1C;
    padding: 1em 1em;
    color: white;
    display: flex;
    justify-content: center;
    font-weight: bold;
    
}

.signin-container-error{
    color: red;
}

.signin-form-text-input{
    margin: .5em 1em;
    flex: 1;
}

.signin-form-part{
    display: flex;
    flex-direction: column;
    
   margin: 1em 2em;
}
.signin-form-part > button{
    flex: 1;
    padding: 1em;
    margin: 1.5em;
    background-color: #FF1B1C;
    font-weight: bold;
    border: none;
    border-radius: 2em;
    color: white;
}
.signin-form-part > button:hover{
    background-color: rgb(192, 29, 0);
}

.signin-container-signin{
    margin: 1em;
}