
.product-main {
    display: flex;
    padding: 1em;
    margin: 0.5em auto;
    margin-bottom: 1em;
    max-width: 40rem;
    flex-direction: column;    
    box-shadow: .1px .1px 2px  #888888;
}

.product-main-img {
    max-width:50% !important;
    height:auto;
    display:block;
    margin-bottom: .5em;
    padding: 2em;
    margin: 0em auto;
}
.product-main-h4 {
   font-weight: bold;
   color: #182024;
}

.product-main-button {
    background-color: yellow;
    padding: 1em;
    border: none;
    margin: 1em;
    font-weight: bold;
 }

 .product-main-button:hover{
    background-color: rgb(255, 157, 0);
 }

 .product-main-button:disabled{
    background-color: rgb(170, 170, 170);
    cursor: not-allowed;
 }

div.description{
    word-break: break-all;
    word-wrap: break-word;
}

div.description img{
    max-width: 100%;
}

.cantfind {
    margin: auto 1em;
    width: 10em;
}

.notfound{
    margin: 5em;
    color: red;
    text-align: center;
}